.Content{
    background-color: rgb(52, 63, 75);
    display:flex;
    flex-direction: column;
    height: 100vh;
    width: 22vw;
    margin-right : 0 !important;
    margin-left : 0 !important;
    padding-right : 0 !important;
    padding-left : 0 !important;
    padding-bottom: 0 !important;
}

.options{
    margin-top: 70px;
    margin-left: 16px;
}

#option{
    color:rgb(52, 63, 75);
    font-size: 20px;
    width: 20vw;
    height: 50px;
    background-color: white;
    margin: 6px;
    text-align: left;
    padding-left: 50px;
}

#option:hover{
    background-color: rgb(239, 181, 191);
}

#option::after{
    background-color:white;
    -webkit-transition: all 0.35s;
    transition: all 0.35s;
}

@media (max-width: 512px){
    #option{
        color:rgb(52, 63, 75);
        font-size: 14px;
        width: 20vw;
        height: 50px;
        background-color: white;
        margin: 6px;
        text-align: left;
        padding-left: 20px;
    }
    #option:hover{
        background-color: rgb(239, 181, 191);
    }
    
    #option::after{
        background-color:white;
        -webkit-transition: all 0.35s;
        transition: all 0.35s;
    }
}