.HistoriaExtra{
    background-color: rgb(52, 63, 75);
    display:flex;
    flex-direction: column;
    height: 100vh;
    width: 100wh;
    margin-right : 0 !important;
    margin-left : 0 !important;
    padding-right : 0 !important;
    padding-left : 0 !important;
    padding-bottom: 0 !important;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
}
    
.Title_Column{
    margin-left: 100px;
}

#title_historiaExtra{
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 60px;
    text-align: center;
    padding-top: 100px;
}

#icono_historiaExtra{
    color:white;
    padding-left: 80px;
    padding-top: 10px;
    height: 60px;
}

#Grafico_hExtra{
    width: 1000px;
    height: 600px;
}

@media (max-width: 512px){
        .Title_Column{
            margin-left: 20px;
        }
        #title_historiaExtra{
            color: white;
            font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            font-size: 40px;
            text-align: center;
            padding-top: 100px;
        }
        #Grafico_hExtra{
            width: 250px;
            height: 220px;
            margin-left: 140px;
            margin-top: 100px;
        }
        #icono_historiaExtra{
            color:white;
            padding-left: 20px;
            padding-top: 10px;
            height: 60px;
        }
}
    